
import axios from 'axios'
// import { callbackify } from 'util';



axios.defaults.withCredentials = true


var rest_api_url_debug = 'http://dev.dea30.net:11080/api/';
var rest_api_url_release = 'https://star.bitwormhole.com/api/';
var rest_api_url = null;



function window_console_log(text) {
    var win = window;
    if (win == null) return;
    var console = win.console;
    if (console == null) return;
    console.log(text);
}


function getRestApiUrl() {
    var url = rest_api_url;
    if (url == null) {
        var env = process.env.NODE_ENV;
        var debug = (env == 'development');
        url = debug ? rest_api_url_debug : rest_api_url_release;
        rest_api_url = url;
        window_console_log("runtime.env: " + env);
        window_console_log("rest_api_url: " + url);
    }
    return url;
}


function makeUrl(context, params) {
    var id = params.id;
    var type = params.type;
    var parameters = params.parameters;

    var url = getRestApiUrl() + type;
    if (id != null) { url = url + '/' + id; }

    if (parameters != null) {
        var sep = '?';
        var query = '';
        for (var name in parameters) {
            var value = parameters[name];
            query = query + sep + name + '=' + value;
            sep = '&';
        }
        if (query.length > 0) { url = url + query; }
    }

    return url;
}


function sendRestRequest(context, params) {

    var on_error = params.on_error;
    var on_ok = params.on_ok;
    var method = params.method;
    var data = params.data;
    var url = makeUrl(context, params);
    var task = null;

    if ("GET" == method) {
        task = axios.get(url);

    } else if ("POST" == method) {
        task = axios.post(url, data);

    } else if ("PUT" == method) {
        task = axios.put(url, data);

    } else if ("DELETE" == method) {
        task = axios.delete(url);

    } else {
        throw new Exception("unsupported method: " + method);
    }

    task.then((resp) => {

        var result = makeRestResultOK(resp);
        callback(on_ok, result);

    }).catch((resp) => {

        var result = makeRestResultError(resp);
        context.dispatch('error/handle', result, { root: true });
        callback(on_error, result);

    });

}

function makeRestResultOK(resp) {
    return resp.data;
}

function makeRestResultError(resp) {
    return resp.data;
}


function callback(fn, result) {
    if (fn != null) {
        fn(result);
    }
}


export default {

    name: "rest",

    namespaced: true,

    state: {},

    getters: {},

    mutations: {},

    actions: {

        get(context, params) {
            params.method = 'GET';
            sendRestRequest(context, params);
        },

        post(context, params) {
            params.method = 'POST';
            sendRestRequest(context, params);
        },

        put(context, params) {
            params.method = 'PUT';
            sendRestRequest(context, params);
        },

        delete(context, params) {
            params.method = 'DELETE';
            sendRestRequest(context, params);
        },

    },

};
